$primaryTextColor: #86e543; //*   -  , " */
$secondaryTextColor: #ffffff; //*    .      (, )   */
$textareaTextColor: #cdcdcd; //*      */
// $socialLinkColor: #ffffff;  //* */
$shopNowButton: #000000; //*     "shop-now" */
$secondTextColor: #8d9289; //*   ,   ,   */
$linkTextColor: #8d9289; //*   ,   ,   */
$primaryBgColor: #01383a; //*   , ,   */

$footerBgColor: #001414; //*   , 80%    */
$mobileBgColor: #001414; //*     */
$dropdownBgColor: #17573d; //*     */

$btnBgColor: #86e543; //*    */
$curlineBottomColor: #28780d; //*      */
$curlineTopColor: #7be34a; //*      */

$hoverColor: #86e543;

$boxShadow: 0px 8px 20px rgba(134, 229, 67, 0.5),
  0px 4px 10px rgba(255, 255, 255, 0.1);

//* =====  ===== */

$gradBn1Text: linear-gradient(
  270deg,
  #1edde0 13.26%,
  #8bda41 51.01%,
  #ef001b 107.89%
); //*    1   */

$gradBn2Text: linear-gradient(
  270deg,
  #03fe8f 4.61%,
  #ebdcff 57.91%,
  #ff98f3 92.2%
); //*    2   */

$gradBn3Text: linear-gradient(
  270deg,
  #ffffff 13.26%,
  #693cf9 60.87%,
  #f475eb 107.89%
); //*    3   */

$primaryBorderRadius: 16px;
$primaryFontSize: 22px;
$mainFont: 'Roboto-medium', sans-serif;

//*  */
$durationFunction: 250ms;
$timingFunction: cubic-bezier(0.4, 0, 0.2, 1);

// ==
$lineShink: 4px;
